@import '../reference/reference';

$block: 'panel';

.#{$block} {
	&__heading {
		color: $grey-dark;
		font-size: 2rem;
		font-weight: $font-weight-semibold;
		line-height: 1.2;
		margin: 1.5rem 0;

		@include media-min(lg) {
			font-size: 2.5rem;
		}
	}

	&__content {
		font-size: 1rem;
		line-height: 1.7;
		margin-bottom: 1.5rem;

		@include media-min(lg) {
			font-size: 1.125rem;
		}
	}
}
