@import '../reference/reference';

$block: 'utility-nav';

.#{$block} {
	display: none;

	@include media-min(lg) {
		display: flex;
		justify-content: end;
	}

	&__anchor {
		color: $grey;
		display: inline-block;
		font-size: 0.75rem;
		font-weight: $font-weight-semibold;
		margin-top: -1px;
		padding-left: 1.25rem;
		padding-right: 1.25rem;
		pointer-events: auto;
		text-transform: uppercase;

		&::before {
			background-color: $white;
			content: '';
			display: block;
			height: 1px;
			position: relative;
			top: -15px;
			width: 100%;
		}

		&:focus,
		&:hover {
			color: $blue;

			&::before {
				background-color: $blue;
			}
		}
	}

	&__item {
		display: flex;
		justify-content: center;
	}

	&__list {
		display: flex;
		list-style-type: none;
		margin: 0;
		padding: 0;
		pointer-events: none;
	}
}
