@import '../reference/reference';

$block: 'button';

.#{$block} {
	@include button;

	&--action {
		background-image: url("data:image/svg+xml,%3Csvg width='19' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 7.5h17.143' stroke='%23fff' stroke-width='1.5'/%3E%3Cpath d='M11.143 1.5l6 6-6 6' stroke='%23fff' stroke-width='1.5' stroke-linecap='square'/%3E%3C/svg%3E");
		background-position: right 24px center;
		background-repeat: no-repeat;
		padding-right: 3.5rem;

		&:focus,
		&:hover {
			background-image: url("data:image/svg+xml,%3Csvg width='19' height='15' viewBox='0 0 19 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 7.50049H17.1429' stroke='%23202020' stroke-width='1.5'/%3E%3Cpath d='M11.1431 1.5L17.1431 7.5L11.1431 13.5' stroke='%23202020' stroke-width='1.5' stroke-linecap='square'/%3E%3C/svg%3E");
		}
	}

	&--dark {
		background-color: $blue-regal;
		background-image: url("data:image/svg+xml,%3Csvg width='19' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 7.5h17.143' stroke='%23fff' stroke-width='1.5'/%3E%3Cpath d='M11.143 1.5l6 6-6 6' stroke='%23fff' stroke-width='1.5' stroke-linecap='square'/%3E%3C/svg%3E");

		&:focus,
		&:hover {
			background-color: transparent;
			background-image: url("data:image/svg+xml,%3Csvg width='19' height='15' viewBox='0 0 19 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 7.50049H17.1429' stroke='%23202020' stroke-width='1.5'/%3E%3Cpath d='M11.1431 1.5L17.1431 7.5L11.1431 13.5' stroke='%23202020' stroke-width='1.5' stroke-linecap='square'/%3E%3C/svg%3E");
			color: $blue-regal;
		}
	}

	&--nav {
		padding-bottom: 7px;
		padding-left: 20px;
		padding-right: 19px;
		padding-top: 9px;
	}

	&:focus .button__svg-container,
	&:hover .button__svg-container {
		left: 0;
		margin-left: 0.5rem;
		width: 5px;

		svg {
			height: 100%;
			opacity: 1;
		}
	}
}
